.background {
  display: flex;
  position: fixed;
  z-index: 2002;
  height: 35px;
  right: calc(20px + calc(var(--diff) * 1px)) !important;
  bottom: 20px;
  border-radius: 4px;
}

.buttonSettings {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  z-index: 2001 !important;
  padding: 0vw !important;
  min-width: 0vh !important;
  width: 35px !important;
  height: 35px !important;
}

.moveLeft {
  /* background: red; */ /* DEV */
  display: flex;
  position: absolute;
  bottom: 0px;
  width: 35px;
  height: 35px;
  border-radius: 4px;
  left: 0px;
  transition: left .8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.moveTop {
  /* background: red; */ /* DEV */
  display: flex;
  position: absolute;
  right: 0px;
  width: 35px;
  height: 35px;
  border-radius: 4px;
  top: 0px;
  transition: top .8s cubic-bezier(0.22, 0.61, 0.36, 1);
}